.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}
.bg-img{
   width: 100%;
   height: 100%;
   background-size: cover;
   background-position: center;
   object-fit: cover;
}
.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color:rgba(27, 27, 27, 0.596);
    opacity: 0.9;
}
.hero {
    width: 100%;
    height: 100%;
}
.hero .content {
   
    position: absolute;
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
    text-align: center;
    margin: 0 auto;

}
.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
    color: aliceblue;
}
.hero .content p {
    font-size: 1.4rem;
    font-weight: 400;
    text-transform: uppercase;
    color: aliceblue;
}
.content .btn {
    margin: 1rem 1rem;
}
@media screen and(max-width:640px) {
    .hero .content h1 {
        font-size: 2rem;
        padding: 0.6rem 0 1.5rem;
        color: aliceblue;
    }
    .hero .content p {
        font-size: 1rem;
        text-transform: uppercase;
        color: aliceblue;
    }
    .hero {
        display: grid;
        grid-template-columns: repeat(auto-fit,minmax(200px,1fr));

    }
}